html {
  scroll-behavior: smooth;
}
.Navbar {
  position: fixed;
  top: 0px;
  display: flex;
  width: 100%;
}
.Navbar a:nth-last-child(3){
  margin-left: auto; /* Push the last three headers to the right */
}

.Navbar a{
  color: rgb(198, 196, 196);
  font-weight: bold;
  font-size: 35px;
  text-decoration: none;
  padding: 10px 15px;

}
.Navbar a:first-child {
  color: rgb(198, 196, 196);
  font-weight: bold;
  font-size: 35px;
  text-decoration: none;
  padding: 10px 15px;

}


.Navbar a:hover{
  color:white;
  transform: scale(1.005);
}


#Section1 {
  color: #ffffff;
  font-size: 40px;
  text-align: left;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
#Section1 p{
    margin: 0; /* adds 1 unit of spacing after each paragraph */
}

.Buttons{
  position: absolute;
  left: 50%;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: space-around;
  -webkit-transform: translate(-50%, 0%);
  transform: translate(-50%, -0%);
  
}
.Buttons button {
  background-color: transparent;
  
  border: none;
}

.Buttons .button-img:hover{
  transform: translate(0%, -2%);
}


.button-img {
  width: 70px;
  height: 70px;
}

body{
  background-color: #1c1b1b
}

